<template>
  <main class="team">
    <Navbar />
    <div
      class="team-layout"
      :class="{
        'member-layout': team.createdBy !== $store.state.user.username,
      }"
    >
      <h2>
        <span v-if="!editing">{{ team.teamName }}</span>
        <span v-else><input type="text" v-model="team.teamName" /></span>
        <span class="buttons" v-if="team.createdBy === $store.state.user.username">
            <button @click="saveTeamName()" v-if="editing">Save</button>
            <button v-else @click="editing=!editing">Edit</button>
        </span>   
      </h2>
      <table class="members">
        <tr>
          <th>Role</th>
          <th>Username</th>
          <th style="max-width: 150px">Action</th>
        </tr>
        <tr>
          <td>Owner</td>
          <td>{{ owner.username }}</td>
          <td></td>
        </tr>
        <tr v-for="member in members" :key="member.id">
          <td>Member</td>
          <td>{{ member.username }}</td>
          <td class="button-cell">
            <button
              v-if="team.createdBy === $store.state.user.username"
              @click="removeMember(member)"
            >
              Remove
            </button>
            <button
              v-if="member.username === $store.state.user.username"
              @click="leave()"
            >
              Leave
            </button>
          </td>
        </tr>
      </table>
      <div
        class="requests"
        v-if="team.createdBy === $store.state.user.username"
      >
        <h4>Team Requests</h4>
        <table v-if="joinRequests.length >= 1">
          <tr>
            <th>Username</th>
            <th style="max-width: 200px">Action</th>
          </tr>
          <tr v-for="request in joinRequests" :key="request.teamRequestId">
            <td v-if="isLoaded">{{ request.username }}</td>
            <td class="button-cell">
              <button @click="acceptJoinRequest(request.teamRequestId)" style="margin-right: 20px">Accept</button>
              <button @click="declineJoinRequest(request.teamRequestId)">Decline</button>
            </td>
          </tr>
        </table>
        <p v-else>No requests to display at the moment.</p>
      </div>
      <div class="invite" v-if="team.createdBy === $store.state.user.username">
        <h4>Invite Member</h4>
        <input type="text" placeholder="username" v-model="invited" />
        <button @click="invite">Invite</button>
      </div>
      <Card :tournament="tournament" />
    </div>
  </main>
</template>

<script>
import UserService from "../services/UserService.js";
import Navbar from "../components/Navbar.vue";
import Card from "../components/Card.vue";
import TeamService from "../services/TeamService.js";
import TournamentService from "../services/TournamentService.js";
import TeamRequestService from "../services/TeamRequestService.js";

export default {
  name: "team",
  components: {
    Navbar,
    Card,
  },
  data() {
    return {
      teamId: 0,
      team: {},
      tournament: {},
      members: [],
      owner: {},
      invited: "",
      editing: false,
      joinRequests: [],
      requestCounts: 0
    };
  },

  created() {
    UserService.getUser(this.$store.state.user.id).then((response) => {
      this.teamId = response.data.team_id;
      this.$store.commit("IMPORT_TEAM_ID", this.teamId);
      TeamService.getTeamById(this.teamId).then((response) => {
        this.team = response.data;
        UserService.getTeamMembers(this.teamId).then((response) => {
          this.members = response.data.filter(
            (member) => this.team.createdBy !== member.username
          );
          this.owner = response.data.find(
            (member) => this.team.createdBy === member.username
          );
        });
      });
      TournamentService.getByTeamId(this.teamId).then((response) => {
        this.tournament = response.data;
      });
    });
    TeamRequestService.getJoinRequests().then((response) => {
      this.joinRequests = response.data;
      this.joinRequests.forEach(request => {
        UserService.getUser(request.senderId).then((response) => {
          request.username = response.data.username;
          this.requestCounts++;
        });
      });
    });
    
  },

  computed: {
    isLoaded() {
      return this.requestCounts == this.joinRequests.length
    }
  },

  methods: {
    invite() {
      if (this.members.length >= 4) {
        alert("Your team is already at the maximum allowed number of members.");
        return;
      } else if (
        this.members.filter((member) => member.username == this.invited)
          .length >= 1
      ) {
        alert("This member is already on your team.");
        return;
      }
      TeamRequestService.invite(this.$store.state.user.id, this.invited).then(
        (response) => {
          if (response.status === 200 || response.status === 201) {
            alert("You have invited the user.");
            this.invited = "";
          }
        }
      );
    },

    acceptJoinRequest(id) {
      TeamRequestService.acceptJoinRequest(id).then((response) => {
        if (response.status == 200 || response.status == 201) {
          alert("You have accepted the join request.");
          this.$router.go(0);
        }
      });
    },

    declineJoinRequest(id) {
        TeamRequestService.declineJoinRequest(id).then((response) => {
            if (response.status == 200 || response.status == 201) {
                alert("You have declined the join request.")
                this.joinRequests = this.joinRequests.filter(request => request.teamRequestId != id)
                this.requestCounts--;
            }
        })
    },

    removeMember(member) {
      TeamService.removeMember(this.teamId, member.id).then((response) => {
        if (response.status === 200) {
          alert(`You have removed ${member.username} from your team.`);
          this.members = this.members.filter(
            (m) => m.username !== member.username
          );
        }
      });
    },

    leave() {
      UserService.leaveTeam().then((response) => {
        if (response.status == 200 || response.status == 201) {
          alert("You have left the team.");
        }
        this.$router.push({ name: "home" });
      });
    },
    saveTeamName() {
      TeamService.editTeam({'teamId': this.teamId, 'teamName': this.team.teamName})
        .then((response) => {
          if (response.status === 200) {
            this.editing = false
            alert("Team name updated successfully.");
          }
        })
  
    },
  },
};
</script>


<style scoped>
.team {
  background: rgb(35, 34, 37);
  background: radial-gradient(
    circle,
    rgba(35, 34, 37, 1) 0%,
    rgba(26, 25, 30, 1) 78%
  );
  width: 100vw;
  height: 100vh;
  background-size: cover;
  color: white;
}
.team-layout {
  margin: 40px;
  display: grid;
  grid-template-areas:
    "header header card"
    "members members card"
    "requests invite card";
  grid-template-rows: 1fr 6fr min-content;
  grid-template-columns: 3fr 1.5fr 3fr;
  row-gap: 50px;
  column-gap: 10px;
}
.member-layout {
  grid-template-areas:
    "header card"
    "members card";
  grid-template-columns: 4fr 3fr;
  grid-template-rows: 0.7fr 8fr;
}
h2 {
  grid-area: header;
}
table {
  grid-area: members;
  width: 80%;
  background-image: linear-gradient(to bottom, red 0%, yellow 50%, orange 100%);
  background-origin: border-box;
  border-spacing: 2px;
}
table td,
table th {
  background-color: black;
  padding: 8px;
}
table td {
  padding-left: 15px;
}
.requests {
  grid-area: requests;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.invite {
  grid-area: invite;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: flex-start;
}
.card {
  grid-area: card;
}
.invite button {
  background-color: lightblue;
  width: 80px;
}
.button-cell {
  padding-left: 0;
  text-align: center;
}
h2 {
    display: flex;
    gap: 50px;
}
@media (max-width: 480px) {
    .team {
        height: 100%;
        min-height: 100vh;
        overflow-y: hidden;
    }
    .team-layout {
        margin: 15px;
        margin-right: 0px;
        width: 100%;
        grid-template-areas:
            "header"
            "members"
            "requests"
            "invite"
            "card";
        row-gap: 50px;
        grid-template-columns: 1fr;
        grid-template-rows: auto;
    }
    .member-layout {
        grid-template-areas:
            "header"
            "members"
            "card";
    }
    .card {
        width: 90%;
    }
}
</style>
