<template>
  <form v-on:submit.prevent="createTeam()" class="create-team">
    <div>
      <label for="name">Team Name</label>
      <input type="text" id="name" placeholder="Team Name" v-model="team.teamName"/>
    </div>
    <div>
      <label for="open-to-public">Open to Public?</label>
      <input type="checkbox" id="open-to-public" v-on:change="openToPublic = !openToPublic"/>
    </div>
    <input type="submit" value="Submit" />
  </form>
</template>

<script>
import TeamService from "../services/TeamService.js";
export default {
  name: "create-team",
  props: {
    tournament: {
      type: Object,
    },
  },
  data() {
    return {
      team: {
        teamName: "",
      },
  
      openToPublic: false,
    };
  },
  methods: {
    createTeam() {
        if (this.team.teamName === "") {
          alert("Please input team name.")
          return;
        }
        if (this.$store.state.teamId != null && this.$store.state.teamId != 0) {
          let confirm = window.confirm("You're already in a team, do you want to leave and create a team?")
          if (!confirm) return;
        }
        TeamService.createTeam(this.tournament.tournament_id, this.team, this.openToPublic).then((response) => {
            if (response.status == 200 || response.status == 201) {
            alert("Team created for tournament.");
            this.$router.push({name: "team"})
            }
        })      
        
    },
  },
};
</script>

<style scoped>
.create-team {
  z-index: 1;
  margin-top: 20px;
  padding-top: 20px;
  background-image: radial-gradient(
      circle at 100% 100%,
      transparent 8px,
      #ff0000 8px,
      #ff0000 10px,
      transparent 10px
    ),
    linear-gradient(to right, #ff0000, #eeff00),
    radial-gradient(
      circle at 0% 100%,
      transparent 8px,
      #eeff00 8px,
      #eeff00 10px,
      transparent 10px
    ),
    linear-gradient(to bottom, #eeff00, #ff531a),
    radial-gradient(
      circle at 0% 0%,
      transparent 8px,
      #ff531a 8px,
      #ff531a 10px,
      transparent 10px
    ),
    linear-gradient(to left, #ff531a, #ffa200),
    radial-gradient(
      circle at 100% 0%,
      transparent 8px,
      #ffa200 8px,
      #ffa200 10px,
      transparent 10px
    ),
    linear-gradient(to top, #ffa200, #ff0000);
  background-size: 10px 10px, calc(100% - 20px) 2px, 10px 10px,
    2px calc(100% - 20px);
  background-position: top left, top center, top right, center right,
    bottom right, bottom center, bottom left, center left;
  background-repeat: no-repeat;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
}
.create-team > div{
    margin: 20px;
}
label {
    margin-right: 5px;
}
input[type="submit"] {
    margin-bottom: 30px;
    position: relative;
    left: 40%;
    width: 20%;
    background-color: lightblue;
}

</style>