<template>
    <div>
      <table v-if="teams.length > 0">
      <tr>
        <th>Team Name</th>
        <th>Owner</th>
        <th>Action</th>
      </tr>
      <tr v-for="team in filteredTeams" :key="team.team_id">
        <td>{{team.teamName}}</td>
        <td>{{team.createdBy}}</td>
        <td class='button-cell'><button @click="requestToJoin(team.teamId)">Join</button></td>
      </tr>
    </table>
    <p v-else>There are no teams recruiting for this tournament right now.</p>
    </div>
    
    
</template>

<script>
import TeamService from "../services/TeamService.js";
import TeamRequestService from "../services/TeamRequestService.js";
export default {
  name: "team-search",
  data() {
    return {
      teams: [],
    };
  },
  created() {
    TeamService.searchTeams(this.$route.params.tournamentId).then((response) => {
      this.teams = response.data;
    });
  },
  computed: {
    filteredTeams() {
      return this.teams.filter(
        (team) => team.teamId != this.$store.state.teamId
      );
    },
  },
  methods: {
    requestToJoin(id) {
      if (
        this.$store.state.user.teamId != null &&
        this.$store.state.user.teamId != 0
      ) {
        let confirm = window.confirm(
          "You're already in a team, do you want to request to join another team?"
        );
        if (!confirm) return;
      }
      TeamRequestService.requestToJoin(id).then((response) => {
        if (response.status == 200 || response.status == 201) {
          this.teams = this.teams.filter((team) => team.teamId != id);
          alert("You have requested to join the team.");
        }
      });
    },
  },
};
</script>

<style scoped>
p {
  color: white;
  margin: 50px;
}
table {
  color: white;
  width: 80%;
  background-image: linear-gradient(to bottom, red 0%, yellow 50%, orange 100%);
  background-origin: border-box;
  border-spacing: 2px;
  margin: 50px;
}
table td,
table th {
  background-color: black;
  padding: 8px;
}
table td {
  padding-left: 15px;
}
.button-cell {
  padding-left: 0;
  text-align: center;
}
</style>