<template>
  <div class="wholeBracket">
    <div class="buttons">
      <button
        @click="generateBracket()"
        v-if="
          tournament.tournament_owner == $store.state.user.username &&
          this.matches.length == 0
        "
      >
        Generate Bracket
      </button>
      <button
        @click="startTournament()"
        v-if="
          tournament.tournament_owner == $store.state.user.username &&
          tournament.status == 'Upcoming' &&
          this.matches.length > 0
        "
      >
        Start Tournament
      </button>
    </div>
    <bracket :flat-tree="rounds">
      <template #player="{ player }">
        {{ player.name }}
        <button
          @click="progressMatch(player.matchId, player.id)"
          v-if="
            player.round == currentRound &&
            tournament.status == 'Active' &&
            tournament.tournament_owner == $store.state.user.username
          "
        >
          Win
        </button>
      </template>
      <template #player-extension-bottom="{ match }">
        {{ match.title }}
      </template>
    </bracket>

    <div class="Rankings" v-if="matches.length > 0 && matches.filter(match => match.round == 4)[0].winner_id">
      <h2>Top 3 Teams</h2>
      <ol>
        <li v-for="(player, index) in topThreePlayers" :key="index">
          {{ player }}
        </li>
      </ol>
    </div>
    <div class="TournamentInfo">
      <!-- Current Tournament Card here -->
      <card :tournament="tournament" />
      <div>
        <button v-if="tournament.status == 'Open'" @click="viewPublicTeams()">
          View Public Teams
        </button>
        <button v-if="tournament.status == 'Open'" @click="createTeam()">
          Create Team
        </button>
        <create-team v-if="showCreateTeam" :tournament="tournament" />
      </div>
    </div>
  </div>
</template>

<script>
import Bracket from "vue-tournament-bracket";
import TournamentService from "../services/TournamentService.js";
import TeamService from "../services/TeamService.js";
import MatchService from "../services/MatchService.js";
import Card from "../components/Card.vue";
import CreateTeam from "../components/CreateTeam.vue";
export default {
  components: {
    Bracket,
    Card,
    CreateTeam,
  },
  data() {
    return {
      showCreateTeam: false,
      matches: [],
      teams: {},
      tournament: {},
      topThreePlayers: [],
      currentRound: 1,
      rounds: [
        // Winners Bracket Round 1
        {
          id: 1,
          next: 5,
          player1: { round: 1, name: "Competitor 1" },
          player2: { round: 1, name: "Competitor 8" },
        },
        {
          id: 2,
          next: 5,
          player1: { round: 2, name: "Competitor 4" },
          player2: { round: 2, name: "Competitor 5" },
        },
        {
          id: 3,
          next: 6,
          player1: { round: 3, name: "Competitor 2" },
          player2: { round: 3, name: "Competitor 7" },
        },
        {
          id: 4,
          next: 6,
          player1: { round: 4, name: "Competitor 3" },
          player2: { round: 4, name: "Competitor 6" },
        },

        // Winners Bracket Round 2
        {
          id: 5,
          next: 7,
          player1: { round: 7, name: "Competitor 1" },
          player2: { round: 7, name: "Competitor 4" },
        },
        {
          id: 6,
          next: 7,
          player1: { round: 8, name: "Competitor 2" },
          player2: { round: 8, name: "Competitor 3" },
        },

        // Winners Bracket Final
        {
          id: 7,
          next: 14, // Connects to Grand Final
          player1: { round: 12, name: "Competitor 5" },
          player2: { round: 12, name: "Competitor 6" },
        },

        // Losers Bracket Round 1
        {
          id: 8,
          next: 10, // Connects to Losers Bracket Round 2
          player1: { round: 5, name: "Competitor 8" },
          player2: { round: 5, name: "Competitor 7" },
        },
        {
          id: 9,
          next: 11, // Connects to Losers Bracket Round 2
          player1: { round: 6, name: "Competitor 5" },
          player2: { round: 6, name: "Competitor 4" },
        },

        // Losers Bracket Round 2
        {
          id: 10,
          next: 12,
          player1: { round: 9, name: "Competitor 1" },
          player2: { round: 9, name: "Competitor 4" },
        },

        // Losers Bracket Round 3
        {
          id: 11,
          next: 12,
          player1: { round: 10, name: "Competitor 7" },
          player2: { round: 10, name: "Competitor 4" },
        },

        // Losers Bracket Round 4
        {
          id: 12,
          next: 13,
          player1: { round: 11, name: "Competitor 6" },
          player2: { round: 11, name: "Competitor 4" },
        },

        // Losers Bracket Final
        {
          id: 13,
          next: 14, // Connects to Grand Final
          player1: { round: 13, name: "Competitor 7" },
          player2: { round: 13, name: "Competitor 6" },
        },

        // Grand Final
        {
          id: 14,
          player1: { round: 14, name: "Competitor 5" },
          player2: { round: 14, name: "Competitor 7" },
        },
      ],
    };
  },

  created() {
    TournamentService.get(this.$route.params.tournamentId).then((response) => {
      this.tournament = response.data;
    });
    TeamService.getAllTeams(this.$route.params.tournamentId).then(
      (response) => {
        let importedTeams = response.data;
        importedTeams.forEach((element) => {
          this.teams[element.teamId] = element.teamName;
        });
        this.importMatches();
      }
    );
  },
  methods: {
    createTeam() {
      if (
        Object.keys(this.teams).filter((id) => id == this.$store.state.teamId)
          .length > 0 &&
        !this.showCreateTeam
      ) {
        alert("Your team is already in this tournament.");
        return;
      } else if (
        this.tournament.tournament_owner == this.$store.state.user.username
      ) {
        alert("You are the host of this tournament.");
        return;
      }
      this.showCreateTeam = !this.showCreateTeam;
    },
    viewPublicTeams() {
      if (
        Object.keys(this.teams).filter((id) => id == this.$store.state.teamId)
          .length > 0 &&
        !this.showCreateTeam
      ) {
        alert("Your team is already in this tournament.");
        return;
      } else if (
        this.tournament.tournament_owner == this.$store.state.user.username
      ) {
        alert("You are the host of this tournament.");
        return;
      }
      this.$router.push({
        name: "team-search",
        params: { tournamentId: this.tournament.tournament_id },
      });
    },
    startTournament() {
      this.tournament.status = "Active";
      TournamentService.start(this.$route.params.tournamentId);
    },
    progressMatch(matchId, playerId) {
      this.currentRound++;
      

      let currentMatch = this.matches.filter(
        (match) => match.match_id == matchId
      )[0];
      currentMatch.winner_id = playerId;
      let currentRound = this.rounds.filter(
        (round) => round.matchId == matchId
      )[0];
      if (currentRound.player1.id == playerId) {
        currentRound.player1.winner = true;
        currentRound.player2.winner = false;
      } else {
        currentRound.player2.winner = true;
        currentRound.player1.winner = false;
      }
      MatchService.progressMatch(
        this.tournament.tournament_id,
        currentMatch
      ).then((response) => {
        let newMatches = response.data;
        newMatches.forEach((newMatch) => {
          let updatedMatch = this.matches.filter(
            (match) => match.match_id == newMatch.match_id
          )[0];
          updatedMatch.first_team_id = newMatch.first_team_id;
          updatedMatch.second_team_id = newMatch.second_team_id;
          let round = this.rounds.filter(
            (round) => round.matchId == newMatch.match_id
          )[0];
          round.player1.name = this.teams[newMatch.first_team_id];
          round.player2.name = this.teams[newMatch.second_team_id];
          round.player1.id = newMatch.first_team_id;
          round.player2.id = newMatch.second_team_id;
        });
      });
      if (this.currentRound > 14) {
        this.displayTopPlayers();
      }
    },

    getWinnerName(matchId) {
      const match = this.matches.find((m) => m.match_id === matchId);
      return match ? this.teams[match.winner_id] : "";
    },

    getLosersNames(matchId) {
      const match = this.matches.find((m) => m.match_id === matchId);
      const losers = [];
      if (match) {
        if (match.first_team_id !== match.winner_id) {
          losers.push(this.teams[match.first_team_id]);
        }
        if (match.second_team_id !== match.winner_id) {
          losers.push(this.teams[match.second_team_id]);
        }
      }
      return losers;
    },

    displayTopPlayers() {
      let winnerName = "",
        secondPlaceName = "",
        thirdPlaceName = "";
      if (this.rounds.find((round) => round.id === 14).player1.winner == true) {
        winnerName = this.rounds.find((round) => round.id === 14).player1.name;
        secondPlaceName = this.rounds.find((round) => round.id === 14).player2
          .name;
      } else {
        winnerName = this.rounds.find((round) => round.id === 14).player2.name;
        secondPlaceName = this.rounds.find((round) => round.id === 14).player1.name;
      }
      if (this.rounds.find((round) => round.id === 13).player1.winner == true) {
        thirdPlaceName = this.rounds.find((round) => round.id === 13).player2
          .name;
      } else {
        thirdPlaceName = this.rounds.find((round) => round.id === 13).player1
          .name;
      }
      this.topThreePlayers = [winnerName, secondPlaceName, thirdPlaceName];
    },

    generateBracket() {
      if (Object.keys(this.teams).length < 8) {
        alert("There are not enough teams to generate bracket.")
        return;
      }
      TournamentService.generateBracket(this.$route.params.tournamentId).then(
        (response) => {
          if (response.status == 200 || response.status == 201) {
            alert("Successfully created bracket.");
            this.$router.go(0);
          }
        }
      );
    },

    importMatches() {
      MatchService.getAllMatches(this.$route.params.tournamentId).then(
        (response) => {
          this.matches = response.data;
          for (let i = 0; i < 14; i++) {
            let j = 0;
            if (i >= 8) j = -1;
            else if (i == 7) j = 6;
            this.rounds[i + j].matchId = this.matches[i].match_id;
            this.rounds[i + j].player1.name =
              this.teams[this.matches[i].first_team_id];
            this.rounds[i + j].player2.name =
              this.teams[this.matches[i].second_team_id];
            this.rounds[i + j].player1.matchId = this.matches[i].match_id;
            this.rounds[i + j].player2.matchId = this.matches[i].match_id;
            this.rounds[i + j].player1.id = this.matches[i].first_team_id;
            this.rounds[i + j].player2.id = this.matches[i].second_team_id;
            if (
              this.matches[i].winner_id != "" &&
              this.matches[i].winner_id == this.matches[i].first_team_id
            ) {
              this.rounds[i + j].player1.winner = true;
              this.rounds[i + j].player2.winner = false;
              this.currentRound = Math.max(
                this.currentRound,
                this.rounds[i + j].player1.round + 1
              );
            } else if (this.matches[i].winner_id != "") {
              this.rounds[i + j].player1.winner = false;
              this.rounds[i + j].player2.winner = true;
              this.currentRound = Math.max(
                this.currentRound,
                this.rounds[i + j].player1.round + 1
              );
            }
          }
          if (this.matches[6].winner_id != 0 && this.matches[6].winner_id != null) {
            this.displayTopPlayers();
          }
        }
      );
    },
  },
};
</script>
<style scoped>
.TournamentInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.wholeBracket {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  color: white;
}
.buttons {
  display: flex;
  flex-direction: column;
}
button {
  border-radius: 5px;
  padding: 5px 8px;
  border: 1px solid #ff3860;
  margin-left: 10px;
  background-color: black;
  color: white;
}
</style>
