import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

/*
 * The authorization header is set for axios when you login but what happens when you come back or
 * the page is refreshed. When that happens you need to check for the token in local storage and if it
 * exists you should set the header so that it will be attached to each request
 */
const currentToken = localStorage.getItem('token')
const currentUser = JSON.parse(localStorage.getItem('user'));

if (currentToken != null) {
  axios.defaults.headers.common['Authorization'] = `Bearer ${currentToken}`;
}

export default new Vuex.Store({
  state: {
    token: currentToken || '',
    user: currentUser || {},
    teamId: '',
    tournaments: [],
    tournament: [
      {
        name: 'GTA5',
        teams: ['team1', 'team2', 'team3', 'team4', 'team5', 'team6', 'team7', 'team8'],
        isActive: false
      },
      {
        name: 'FIFA',
        teams: ['team1', 'team2', 'team3', 'team4', 'team5', 'team6', 'team7', 'team8'],
        isActive: false,
      },
      {
        name: 'Madden',
        teams: ['team1', 'team2', 'team3', 'team4', 'team5', 'team6', 'team7', 'team8'],
        isActive: false,
      },
      {
        name: 'street combat',
        teams: ['team1', 'team2', 'team3', 'team4', 'team5', 'team6', 'team7', 'team8'],
        isActive: false,
      },
      {
        name: 'dot2',
        teams: ['team1', 'team2', 'team3', 'team4', 'team5', 'team6', 'team7', 'team8'],
        isActive: false,
      },
    ],
    teams: [
      {id: 1, name: 'team 1', isOpenToMemebers:true},
      {id: 2, name: 'team 2', isOpenToMemebers:false},
      {id: 3, name: 'team 3', isOpenToMemebers:true},
      {id: 4, name: 'team 4', isOpenToMemebers:true},
      {id: 5, name: 'team 5', isOpenToMemebers:false},
      {id: 6, name: 'team 6', isOpenToMemebers:false},
      {id: 7, name: 'team 7', isOpenToMemebers:true},
      {id: 8, name: 'team 8', isOpenToMemebers:false},

    ]
  
  },
  mutations: {
    IMPORT_TEAM_ID(state, teamId) {
      state.teamId = teamId;
    },
    IMPORT_TOURNAMENTS(state, tournaments) {
      state.tournaments = tournaments;
    },
    SET_TEAMS(state, teams) {
      state.teams = teams;
    },
    SET_AUTH_TOKEN(state, token) {
      state.token = token;
      localStorage.setItem('token', token);
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
    },
    SET_USER(state, user) {
      state.user = user;
      localStorage.setItem('user', JSON.stringify(user));
    },
    
    LOGOUT(state) {
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      state.token = '';
      state.user = {};
      axios.defaults.headers.common = {};
    }
  }
})
