<template>
  <div class="main">
      <navbar />
      <div class="page">
          <card :tournament="tournament" class="card" />
          <side-bar class="side-bar"></side-bar>
          <team-search class="search"></team-search>
      </div>
  </div>
</template>

<script>
import Navbar from "../components/Navbar.vue";
import Card from "../components/Card";
import SideBar from "../components/SideBar.vue"
import TeamSearch from "../components/TeamSearch.vue"
import TournamentService from "../services/TournamentService.js"
export default {
    components: {
        TeamSearch, Navbar, Card, SideBar,
    },
    data() {
        return {
            tournament: {}
        }
    },
    created() {
        TournamentService.get(this.$route.params.tournamentId).then(response => {
            this.tournament = response.data
        })
    }
}
</script>

<style scoped>
.main {
  background: rgb(35, 34, 37);
  width: 100vw;
  height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  overflow-y: hidden
  
}

.page {
  display: grid;
  grid-template-areas:
    "search card sidebar";
    grid-template-columns: 60vw 1fr 15vw;
  gap: 5px;
  height: calc(100vh - 50px)

}
.side-bar {
    grid-area: sidebar
}
.card {
    grid-area: card;
    margin-top: 20px;
}
.search {
    grid-area: search;
    
}
</style>